import React from 'react';
import Load from '../../svg-loaders/audio.svg';
import cor from '../../svg-loaders/hearts.svg';

const LoaderInterno = ()=>{
    return(
        <div style={{position:"absolute",width:"100%",height:"100%",verticalAlign:"middle"}}>
            <img src={cor} alt="" style={{width:"10vh",margin:"auto"}}/>
        </div>
    );
}
export default LoaderInterno;